/* ProjectsStyles.css */

.projects-header {
	display: flex;
	justify-content: center;
	align-items: center;
}

.about-me-button {
	padding: 10px;
	bottom: 20px;
	justify-content: center;
	background-color: #007bff;
	color: white;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	font-size: 16px;
}

.about-me-button:hover {
	background-color: #0056b3;
}
